import { AddProductComponent } from './add-product/add-product.component';
import { ProductService } from './../../services/product.service';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { TooltipPosition } from '@angular/material/tooltip';
import { FormControl } from '@angular/forms';
import { ConfirmationService, PrimeNGConfig, Message } from 'primeng/api';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss'],
  providers: [ConfirmationService]

})



export class ProductsComponent implements OnInit {
  products = [  ]
  positionOptions: TooltipPosition[] = ['below', 'above', 'left', 'right'];
  tooltipPosition = new FormControl(this.positionOptions[0]);
  msgs: Message[] = [];

  position: string;
  
  constructor(
    private productService : ProductService,
    public dialog: MatDialog,
    private dialogRef:MatDialogRef<AddProductComponent>,
    private confirmationService: ConfirmationService, 
    private primengConfig: PrimeNGConfig,
    private toastr : ToastrService
    ) { }

  ngOnInit(): void {
    this.getProducts();
    this.primengConfig.ripple = true;

  }

  
  deleteProductConfirmation(product){
    console.log('delete this', product);
    
    this.confirmationService.confirm({
      message: 'Are you sure that you want to proceed?',
      header: 'Confirmation',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
          this.msgs = [{severity:'info', summary:'Confirmed', detail:'You have accepted'}];
          console.log('accepted');
          this.deleteProduct(product.id);         
          
      },
      reject: () => {
          this.msgs = [{severity:'info', summary:'Rejected', detail:'You have rejected'}];
          console.log('rejected');

      }
  });
  }
 
  
deleteProduct(productId){
  this.productService.deleteProduct(productId).subscribe((data)=>{
    console.log(data);
    this.toastr.success(data.message);
    if(data.status === 200)
    this.getProducts();
    
  },(error)=>{
    console.log(error);
    this.toastr.error(error.message);

  })
}

editProduct(product){
  console.log('edit this', product);
  
  const dialogRef = this.dialog.open(AddProductComponent, {
    width: '700px',
   data: {product:product}
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    this.getProducts();
    // this.animal = result;
  });

}

  getProducts(){
    this.productService.getProducts().subscribe((data)=>{
      console.log(data);
      this.products= data.data;
      
    }, (error)=>{
      console.log(error);
      
    })
  }
  addProductDialog(){
    const dialogRef = this.dialog.open(AddProductComponent, {
      width: '600px',
      // data: {name: this.name, animal: this.animal}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.getProducts();
      // this.animal = result;
    });
  }


  onNoClick(): void {
    this.dialogRef.close();
  }
  }

